import { TIngredientItem } from "../../services/types/types";

export const setDate = (createdAt: string) => {
  const orderDate = new Date(Date.parse(createdAt));
  const timeZone = `${orderDate.getTimezoneOffset() > 0 ? `+${orderDate.getTimezoneOffset() / 60}` : orderDate.getTimezoneOffset() / 60}`;
  return ` i-GMT${timeZone}`;
};

export const sumPrice = (yummy: string[], allIngredients: TIngredientItem[]) => {
  let sum = 0;
  if (allIngredients) {
    yummy.forEach(ing => {
      if (ing !== null) {
        const foundIngredient = allIngredients.find(item => item._id === ing);
        if (foundIngredient) {
          sum += foundIngredient.price;
        }
      }
    });
  }
  return sum;
}

