export const countIngredients = (arr: string[]) => {
  const counts: { [key: string]: number } = {};
  arr.forEach(el => {
    const item = el;
    counts[item] = counts[item] ? counts[item] + 1 : 1;
  });
  const number: { [key: string]: number } = {};
  for (const item in counts) {
    if (counts[item] >= 1) {
      number[item] = counts[item];
    }
  };
  return number;
};
