import Style from './order-details.module.css'
import { useAppSelector } from "../../services/types/types";

export default function OrderDetails() {
  const orderNumber = useAppSelector(state => state.order.number);
  return ( 
    <div className={Style.content}>
      <p className={`text text_type_digits-large ${Style.number}`}>{orderNumber}</p>
      <p className="text text_type_main-medium mt-8 mb-15"> </p>
      <div className={Style.image}></div>
      <p className="text text_type_main-default mt-15 mb-2">   </p>
      <p className="text text_type_main-default text_color_inactive">    </p>
    </div>
  );
}

