import Style from "./feed-page.module.css";
import { useEffect, useMemo } from "react";
import { WS_CLOSE_CONNECTION, WS_CONNECTION_START } from "../../services/actions/wsFeed";
import OrderStatus from '../../components/order-status/order-status';
import OrdersTotal from '../../components/orders-totals/orders-total';
import OrdersList from '../../components/orders-list/orders-list';
import { useAppDispatch, useAppSelector } from "../../services/types/types";


export default function FeedPage() {
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch({
      type: WS_CONNECTION_START //  
    });
    return () => {
      dispatch({
        type: WS_CLOSE_CONNECTION
      })
    }
  }, []); // eslint-disable-line

  const orders = useAppSelector(store => store.wsOrders.orders); //     
  const total = useAppSelector(store => store.wsOrders.total); //      
  const totalToday = useAppSelector(store => store.wsOrders.totalToday); //       

  const ordersDone = useMemo(() =>
    orders.filter((item) => item.status === 'done'),
    [orders]
  )
  const ordersInProcess = useMemo(() =>
    orders.filter((item) => item.status !== 'done'),
    [orders]
  )
  if (!orders) {
    return <div>...</div>;
  }

  return (
    <section className={Style.feedPage}>
      <h2 className={`text text_type_main-large`}> </h2>
      <div className={Style.page}>
        <OrdersList />
        <article className={Style.feedTotal}>
          <ul className={Style.ordersStatus}>
            <OrderStatus title=':' orders={ordersDone} done={true} />
            <OrderStatus title=' :' orders={ordersInProcess} done={false} />
          </ul>
          <ul className={Style.ordersTotals}>
            <OrdersTotal title='   :' number={total} />
            <OrdersTotal title='  :' number={totalToday} />
          </ul>
        </article>
      </div>
    </section>
  )
};
